import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    count: 0,
    info_switch: true,
  },
  mutations: {
    increment (state) {
      state.count++
    }
  }
});

export default store;



